var state = require('../lib/state-machine');

/**
 * 
 */
class BloomElement extends state.EntityElement {
    constructor(machine, state, callback) {
        super(machine, state, callback);
    }
    get id() {
        return this.state.id;
    }
    get angle() {
        return this.state.angle;
    }
    get segments() {
        return this.state.segments;
    }
    toString() {
        return JSON.stringify({
            id: this.id,
            parent_id: this.parent ? this.parent.id : null,
            x: this.x,
            y: this.y,
            angle: this.angle,
            segments: this.segments,
            children: this.children.length
        });
    }
    origin(scale) {
        return [this.x*scale,this.y*scale];
    }
    static bloom(el, iter) {
        var getAngle = function (distance, base) {
            return (2 * Math.PI / (base)) * distance;
        };
        var getCartesianCoordinates = function (angle, radius) {
            return [Math.cos(angle)*radius, Math.sin(angle)*radius];
        };
        for(var i=1;i<=el.segments;i++) {
            var _id = el.id + i.toString();
            var angle = (el.angle + (54 * i)) / iter;
            while (angle > 360) angle -= 360;
            var ccoords = getCartesianCoordinates(angle, 1/(iter*2));
            el.children.push(new BloomElement(
                el.machine, { 
                    id: _id, 
                    x: el.x + (ccoords[0]/iter), 
                    y: el.y + (ccoords[1]/iter), 
                    parent: el, 
                    angle: angle,  
                    segments: 5,
                    iteration: iter},
                    BloomElement.bloom));
        }
        return el.children;
    }
}

class FractalOfLife {

    constructor(emerga) {
        this.emerga = emerga;
    }

    init() {
        this.emerga.initCanvas();
        var canvasContext = this.emerga.context;

        var iterop = this.fractalOfLifeBloom;
        var machine = this.stateMachine = new state.StateMachine();
        machine.builders.add(new state.StateBuilder(machine, null, (el, iter) => {
            var nel = new BloomElement(
                machine, {  
                    id: '', 
                    x: 0, 
                    y: 0, 
                    parent: null, 
                    angle: 54,  
                    segments: 5},
                    BloomElement.bloom);
            nel.parent = nel;
            machine.elements.add(nel);
        }));
        machine.operators.add(new state.StateOperator(machine, null, (el, iter) => {
            // execute the operator on the element
            // and add resulting elements to list
            return el.execute(iter);
        }));
        machine.postIteration = (machine, _els) => {
            machine.elements.clear();
            machine.elements.add(_els);
        };
        machine.renderers.add(new state.StateRenderer(machine, null, (el, iter) => {
            var offset = (a, o) => { 
                return [a[0]+o,a[1]+o]; 
            };
            var originCoords = offset(el.origin(256), 128);
            canvasContext.beginPath();
            el.children.forEach((child) => {
                var destCoords = offset(child.origin(256), 128);
                canvasContext.moveTo(originCoords[0], originCoords[1]);
                canvasContext.lineTo(destCoords[0], destCoords[1]);
            });
            canvasContext.stroke();
        }));
    }


    run() {
        this.stateMachine.run(4);
    }

}
module.exports = FractalOfLife;